// 
import Xiaoxiannan from './Xiaoxiannan/index'
import tbButton from './tbButton/index'//tb-button 
import tbRow from './tbRow/index'//tb-row 
import tbCol from './tbCol/index'//tb-col 
import tbLink from './tbLink/index'//tb-link 
import tbContainer from './tbContainer/index'
import tbAside from './tbAside/index'
import tbMain from './tbMain/index'
import tbHeader from './tbHeader/index'
import tbFooter from './tbFooter/index'
import './iconfont/iconfont.css'//icon
import './global.css'
// 
const components = [
    Xiaoxiannan,
    tbButton,
    tbRow,
    tbCol,
    tbLink,
    tbContainer,
    tbAside,
    tbMain,
    tbHeader,
    tbFooter
]

//  install 
const install = function (Vue) {
    if (install.installed) return
    install.installed = true
    // 
    components.map(component => {
        Vue.component(component.name, component)
    })
}

if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue)
}

export default {
    //  install 
    install,
    // 
    ...components
}